<template>
  <div>
    <!-- Header  -->
    <van-nav-bar
      fixed
      title=""
      left-arrow
      @click-left="$router.back()"
    />

    <!--  -->
    <div class="article-container">
      <!--  -->
      <h1 class="art-title">{{ articleObj.title }}</h1>

      <!--  -->
      <van-cell
        center
        :title="articleObj.aut_name"
        :label="$changeTime(articleObj.pubdate)"
      >
        <template #icon>
          <img :src="articleObj.aut_photo" alt="" class="avatar" />
        </template>
        <template #default>
          <div>
            <van-button
              v-if="articleObj.is_followed"
              type="info"
              size="mini"
              @click="followFn(false)"
              ></van-button
            >
            <van-button
              v-else
              icon="plus"
              type="info"
              size="mini"
              plain
              @click="followFn(true)"
              ></van-button
            >
          </div>
        </template>
      </van-cell>

      <!--  -->
      <van-divider></van-divider>

      <!--  -->
      <div class="art-content" v-html="articleObj.content"></div>

      <!--  -->
      <van-divider>End</van-divider>

      <!--  -->
      <div class="like-box">
        <van-button
          icon="good-job"
          type="danger"
          size="small"
          v-if="articleObj.attitude && articleObj.attitude !== -1"
          @click="attitudeFn(false)"
          ></van-button
        >
        <van-button
          v-else
          icon="good-job-o"
          type="danger"
          plain
          size="small"
          @click="attitudeFn(true)"
          ></van-button
        >
      </div>
    </div>

    <!--  -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text=""
      @load="onLoad"
    >
      <Comment :comList="commentList"></Comment>
    </van-list>
    <div :class="comShow ? 'art-cmt-container-1' : 'art-cmt-container-2'">
      <!--  - 1 -->
      <div class="add-cmt-box van-hairline--top" v-if="comShow">
        <van-icon name="arrow-left" size="0.48rem" @click="$router.back()" />
        <div class="ipt-cmt-div" @click="comShow = false"></div>
        <div class="icon-box">
          <van-badge :content="totalCount > 0 ? totalCount : ' '" max="99">
            <van-icon
              name="comment-o"
              size="0.53333333rem"
              @click="comScrollFn"
            />
          </van-badge>
          <van-icon name="star-o" size="0.53333333rem" />
          <van-icon name="share-o" size="0.53333333rem" />
        </div>
      </div>

      <!--  - 2 -->
      <div class="cmt-box van-hairline--top" v-else>
        <textarea
          placeholder=""
          v-focus
          @blur="blurFn"
          v-model.trim="comText"
        ></textarea>
        <van-button type="default" :disabled="comText.length===0" @click="sendComFn"></van-button>
      </div>
    </div>
  </div>
</template>

<script>
import {
  artilceDetailAPI,
  autFollowedAPI,
  unFollowedAPI,
  artLikeAPI,
  unLikeAPI,
  getCommentAPI,
  sendCommentAPI
} from '@/api'
import Comment from './Comment'
export default {
  data () {
    return {
      articleObj: {}, // 
      commentList: [], // 
      comShow: true, // 
      totalCount: 0, // 
      loading: false,
      finished: false,
      lastId: null, // 
      comText: '' // 
    }
  },
  async created () {
    // 
    const [, res] = await artilceDetailAPI(this.$route.params.id)
    this.articleObj = res.data.data
  },
  methods: {
    blurFn () {
      setTimeout(() => { //  1 2.v-ifv-show
        this.comShow = true
      }, 0)
    },
    async getComment () { // 
      const [, res] = await getCommentAPI({
        type: 'a',
        source: this.$route.params.id,
        offset: this.lastId // nullaxios
      })
      if (res?.data?.data?.results.length === 0) {
        this.finished = true //   onload
        return
      }
      this.commentList = [...this.commentList, ...res.data.data.results] // 
      this.totalCount = res.data.data.total_count // 
      this.lastId = res.data.data.last_id// lastid
      this.loading = false //    
    },
    async followFn (bool) { // 
      // 
      this.articleObj.is_followed = bool
      if (bool === true) {
        const [err] = await autFollowedAPI({
          // 
          target: this.articleObj.aut_id
        })
        if (!err) this.$Toast({ message: '' })
      } else {
        const [err] = await unFollowedAPI({
          // 
          target: this.articleObj.aut_id
        })
        if (!err) this.$Toast({ message: '' })
      }
    },
    async attitudeFn (bool) { // 
      // 
      this.articleObj.attitude = bool
      if (bool === true) {
        const [err] = await artLikeAPI({
          target: this.articleObj.art_id
        })
        if (!err) this.$Toast({ message: '' })
      } else {
        const [err] = await unLikeAPI(this.$route.params.id)
        if (!err) this.$Toast({ message: '' })
      }
    },
    comScrollFn () {
      //   
      const now = window.scrollY //   0
      let dist = document.querySelector('.article-container').offsetHeight // 
      if (dist > document.documentElement.scrollHeight - window.innerHeight) {
        dist = document.documentElement.scrollHeight - window.innerHeight
      }
      const step = (dist - now) / 10 // 
      //   console.log(step)
      setTimeout(() => {
        if (Math.abs(step) < 1) {
          window.scrollTo(0, dist)
          return
        }
        window.scrollTo(0, now + step)
        this.comScrollFn()
      }, 10)
    },
    onLoad () { // 
      this.getComment()
    },
    async sendComFn () { // 
      const [, res] = await sendCommentAPI({
        target: this.$route.params.id,
        content: this.comText
      })
      this.commentList.unshift(res.data.data.new_obj) // 
      this.totalCount++ // 
      this.comText = '' // 
      console.log(this.commentList)
    }
  },
  components: {
    Comment
  }
}
</script>

<style scoped lang="less">
.article-container {
  padding: 10px;
  margin-top: 46px;
}
.art-title {
  font-size: 16px;
  font-weight: bold;
  margin: 10px 0;
}

.art-content {
  font-size: 12px;
  line-height: 24px;
  width: 100%;
  overflow-x: scroll;
  word-break: break-all;
}

.van-cell {
  padding: 5px 0;
  &::after {
    display: none;
  }
}

.avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #f8f8f8;
  margin-right: 5px;
  border: none;
}

.like-box {
  display: flex;
  justify-content: center;
}

/* -  -  */
// 
.art-cmt-container-1 {
  padding-bottom: 46px;
}
.art-cmt-container-2 {
  padding-bottom: 80px;
}

//  - 1
.add-cmt-box {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 46px;
  line-height: 46px;
  padding-left: 10px;
  .ipt-cmt-div {
    flex: 1;
    border: 1px solid #efefef;
    border-radius: 15px;
    height: 30px;
    font-size: 12px;
    line-height: 30px;
    padding-left: 15px;
    margin-left: 10px;
    background-color: #f8f8f8;
  }
  .icon-box {
    width: 40%;
    display: flex;
    justify-content: space-evenly;
    line-height: 0;
  }
}

.child {
  width: 20px;
  height: 20px;
  background: #f2f3f5;
}

//  - 2
.cmt-box {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  padding-left: 10px;
  box-sizing: border-box;
  background-color: white;
  textarea {
    flex: 1;
    height: 66%;
    border: 1px solid #efefef;
    background-color: #f8f8f8;
    resize: none;
    border-radius: 6px;
    padding: 5px;
  }
  .van-button {
    height: 100%;
    border: none;
  }
}
</style>
