<template>
  <div>
    <!--  -->
    <van-nav-bar fixed ref="nav">
      <template #left>
        <van-image
          class="logo"
          :src="require('../../assets/toutiao_logo.png')"
        />
        <!-- <img src="../../assets/toutiao_logo.png" alt=""> -->
      </template>
      <template #right>
        <van-icon
          name="search"
          color="white"
          size="0.48rem"
          @click="$router.push({ name: 'Search' })"
        />
      </template>
    </van-nav-bar>
    <!-- tab -->
    <div class="top">
      <!-- @click="tabFn" -->
      <van-tabs v-model="active" sticky offset-top="1.22666667rem">
        <van-tab
          :title="obj.name"
          v-for="obj in userList"
          :key="obj.id"
          :name="obj.id"
        >
          <van-pull-refresh
            v-model="loading"
            @refresh="onRefresh"
            loading-text=" "
          >
            <van-list
              v-model="isLoading"
              :finished="finished"
              finished-text=""
              @load="onLoad"
              offset="10"
            >
              <ArticleList :articleList="articleList" @del="delFn"></ArticleList
            ></van-list>
          </van-pull-refresh>
        </van-tab>
      </van-tabs>
    </div>
    <!--  + -->
    <van-icon
      name="plus"
      size="0.37333333rem"
      class="moreChannels"
      @click="show = true"
    />
    <van-popup v-model="show" :close-on-click-overlay="false" class="edit_wrap">
      <!--  -->
      <van-nav-bar title="">
        <template #right>
          <van-icon
            name="cross"
            size="0.37333333rem"
            color="white"
            @click="closeEdit"
          />
        </template>
      </van-nav-bar>
      <!--  -->
      <ChannelEdit
        v-model="active"
        ref="channel"
        :allList="allList"
        :userList="userList"
        @updateChannel="updateChannelFn"
        @closePop="show = false"
      ></ChannelEdit>
    </van-popup>
  </div>
</template>

<script>
import {
  userChannelAPI,
  allChannelAPI,
  undateChannelAPI,
  getArticleListAPI,
  dislikeAPI,
  reportAPI
} from '@/api'
import ArticleList from './components/ArticleList'
import { Toast } from 'vant'
import ChannelEdit from './ChannelEdit'
export default {
  components: {
    ArticleList, // 
    ChannelEdit // 
  },
  data () {
    return {
      active: 0, // id
      userList: [], // 
      allList: [], // 
      articleList: [], // 
      channelShow: 0, // id
      isLoading: false, // 
      finished: false, // 
      loading: false, // 
      page: 1, // 
      show: false // 
    }
  },
  async created () {
    // 
    const [, res] = await userChannelAPI()
    this.userList = res.data.data.channels
    // 
    const [, res2] = await allChannelAPI()
    this.allList = res2.data.data.channels
    // 
    // this.getArticleList()  //
  },
  methods: {
    // 
    async getArticleList () {
      // 
      const [, article_res] = await getArticleListAPI({
        channel_id: this.channelShow,
        timestamp: new Date().getTime().toString(),
        // 
        // timestamp: 1556789000001,
        with_top: 1,
        page: this.page
      })
      if (article_res.data.data?.results.length === 0) {
        this.finished = true
        return
      }
      //   console.log(article_res)
      this.articleList = [
        ...this.articleList,
        ...article_res.data.data.results
      ]
      this.isLoading = false
      this.loading = false
      // 
      Toast.clear()
    },
    onRefresh () {
      // 
      this.isLoading = false // 
      this.page = 1
      this.articleList = [] //   
      Toast.loading({
        message: '...',
        forbidClick: true,
        duration: 0 //     2   2  
      })
      this.getArticleList()
    },
    onLoad () {
      // 
      this.getArticleList()
      this.page++
    },
    async delFn (art_id, type) {
      // 
      const index = this.articleList.findIndex(obj => obj.art_id === art_id)
      this.articleList.splice(index, 1)
      if (type === undefined) {
        await dislikeAPI({
          // 
          target: art_id
        })
      } else {
        await reportAPI({
          // --
          target: art_id,
          type: type
        })
      }

      this.articleList = []
      this.page = 1
      this.getArticleList()
    },
    closeEdit () {
      //    
      this.show = false
      console.log(this.$refs.channel)
      console.log(this.$refs.nav)
      this.$refs.channel.isShow = false
    },
    async updateChannelFn (userChannelList) {
      // 
      let arr = JSON.parse(JSON.stringify(userChannelList)) // 
      arr = arr.map((obj, index) => {
        delete obj.name
        obj.seq = index + 1
        return obj
      })
      arr.splice(0, 1)
      await undateChannelAPI({
        channels: arr
      })
    }
  },
  watch: {
    // active tabs
    active () {
      this.channelShow = this.active
      this.articleList = []
      this.page = 1
      this.onLoad()
    }
  }
}
</script>

<style scoped lang="less">
.logo {
  width: 115px;
  height: 37px;
}
.top {
  padding-top: 46px;
}
//  tabs 
/deep/ .van-tabs__wrap {
  padding-right: 30px;
  background-color: #fff;
}

// 
.moreChannels {
  position: fixed;
  top: 62px;
  right: 8px;
  z-index: 999;
}

// 
/* */
.edit_wrap {
  width: 100%;
  height: 100%;
}
</style>
