import axios1 from 'axios'
import to from '@/utils/awaitTo' // to-axios, 
import store from '@/store'
import router from '@/router'
import Notify from '@/ui/Notify'
import { removeStorage } from '@/storage'
import { refreshTokenAPI } from '@/api'

var JSONbigString = require('json-bigint')({ storeAsString: true })
const axios = axios1.create({
  baseURL: 'http://toutiao-app.itheima.net', // 
  transformResponse: [
    function (data) {
      if (data.length !== 0) {
        return JSONbigString.parse(data)
      } else {
        return data
      }
    }

  ]
})

// 
axios.interceptors.request.use(function (config) {
  // 
  if (store.state.token) {
    config.headers.Authorization = 'Bearer ' + store.state.token
  }
  return config
}, function (error) {
  // 

  return Promise.reject(error)
})

// 
axios.interceptors.response.use(function (response) {
  // 
  return response
}, async function (error) {
  // 
  if (error?.response?.status === 401) {
    //  - 
    removeStorage('token')
    store.commit('delToken')

    // token1: , , token
    // Notify({ type: 'danger', message: 'token-' })
    // setTimeout(() => {
    //   router.push({
    //     name: 'Login'
    //   })
    // }, 1000) // 500

    // token2: token  (token)
    if (store.state.refresh_token === null) {
      // refresh_token
      Notify({ type: 'danger', message: '' })
      setTimeout(() => {
        router.push({
          name: 'Login'
        })
      }, 1000)
    } else {
      const [err, res] = await refreshTokenAPI() // refresh_tokentoken
      if (err?.response?.status === 403) {
        // refresh_token(/)
        Notify({ type: 'danger', message: '' })
        setTimeout(() => {
          router.push({
            name: 'Login'
          })
        }, 1000)
      } else {
        // refresh_token, token
        // tokenvuex
        store.commit('setToken', res.data.data.token)
        // token, 401, ?
        // 
        console.dir(error)
        error.config.headers.Authorization = res.data.data.token // token
        return axios(error.config) // 
      }
    }
  }
  return Promise.reject(error)
})

// axios.defaults.baseURL = 'http://toutiao-app.itheima.net'
// 
// axios.defaults.baseURL = 'http://api-toutiao-web.itheima.net/app'
export default ({ url, method = 'GET', data = {}, headers = {}, params = {} }) => {
  const PromiseA = axios({
    url,
    method,
    data,
    headers,
    params
  })
  return to(PromiseA) // -
}
