<template>
  <div class="cell_wrap" @click="$router.push(`/article/${artObj.art_id}`)">
    <van-cell >
      <template #title>
        <div class="title-box">
          <span>{{ artObj.title }}</span>
          <!--  -->
          <img
            v-lazy="artObj.cover.images[0]"
            v-if="artObj.cover.type === 1"
            class="thumb"
          />
        </div>
        <!--    -->
        <div class="thumb-box" v-if="artObj.cover.type > 1">
          <img
            v-lazy="str"
            v-for="(str, index) in artObj.cover.images"
            :key="index"
            class="thumb"
          />
        </div>
      </template>
      <template #label>
        <div class="label-box">
          <span>
            {{ artObj.aut_name }}&nbsp;&nbsp;&nbsp;
            {{ artObj.comm_count }}&nbsp;&nbsp;&nbsp;{{
              $changeTime(artObj.pubdate)
            }}
          </span>
          <van-icon name="cross" @click.stop="show = true" v-if="closeShow" />
          <van-action-sheet
            v-model="show"
            get-container="body"
            cancel-text=""
            :actions="actions"
            @select="onSelect"
            @closed="onClosed"
          />
        </div>
      </template>
    </van-cell>
  </div>
</template>

<script>
export default {
  props: {
    artObj: Object,
    closeShow: {
      type: Boolean, // 
      default: true // 
    }
  },
  data () {
    return {
      show: false, // 
      actions: [], // 
      newActions: [// 
        { name: '' },
        { name: '' },
        { name: '' }
      ]
    }
  },
  created () {
    this.actions = this.newActions
  },
  methods: {
    onSelect (action) { // 
      if (action.name === '') {
        this.actions = [
          {
            value: -1,
            name: ''
          },
          {
            value: 0,
            name: ''
          },
          {
            value: 1,
            name: ''
          },
          {
            value: 2,
            name: ''
          },
          {
            value: 3,
            name: ''
          },
          {
            value: 4,
            name: ''
          },
          {
            value: 6,
            name: ''
          },
          {
            value: 8,
            name: ''
          },
          {
            value: 5,
            name: ''
          },
          {
            value: 7,
            name: ''
          }
        ]
      } else if (action.name === '') {
        this.actions = this.newActions
      } else {
        this.$emit('del', this.artObj.art_id, action.value)
      }
    },
    onClosed () {
      this.actions = this.newActions
    }
  }
}
</script>

<style scoped lang="less">
/* .cell_wrap /deep/ .van-icon-cross{
    display: flex;
    align-items: flex-end;
    padding-bottom: 2px;
} */
.title-box {
  // 
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.label-box {
  // label
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.thumb {
  // 
  // 0.618
  width: 113px;
  height: 70px;
  background-color: #f8f8f8;
  object-fit: cover;
}

.thumb-box {
  // 
  display: flex;
  justify-content: space-between;
}
</style>
