import Vue from 'vue'
import { Toast as Toast2 } from 'vant'

const Toast = ({ icon = 'success', message }) => {
  Toast2({
    icon,
    message,
    duration: 1000
  }) // 
  // , 
  // :
  /*
      import {ABC} from 'xxx'
      ABC({
          t: type,
          m: message
      })
    */
}

Vue.prototype.$Toast = Toast
