import axios from '@/utils/request'

// 
export const getUserInfo = () =>
  axios({
    url: '/v1_0/user'
  })

// 
export const getUserProfile = () =>
  axios({
    url: '/v1_0/user/profile'
  })

// 
export const changeUserAvatatr = data =>
  axios({
    url: '/v1_0/user/photo',
    method: 'PATCH',
    data
  })

// /
export const changeUserProfile = data =>
  axios({
    url: '/v1_0/user/profile',
    method: 'PATCH',
    data
  })
