<template>
  <div class="search-result-container">
    <!--  -->
    <van-nav-bar
      title=""
      left-arrow
      @click-left="$router.go(-1)"
      fixed
    />
    <!--  -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text=""
      @load="onLoad"
      offset="10"
    >
      <ArticleItem
      v-for="obj in resultList"
      :key="obj.art_id"
      :artObj="obj"
      :closeShow="false"
    ></ArticleItem>
    </van-list>

  </div>
</template>

<script>
import { searchResultAPI } from '@/api'
import ArticleItem from '@/components/ArticleItem'
export default {
  name: 'SearchResult',
  data () {
    return {
      resultList: [], // 
      loading: false,
      finished: false,
      page: 1 // 
    }
  },
  components: {
    ArticleItem
  },
  methods: {
    async getSearchResult () {
      // 
      const [, res] = await searchResultAPI({
        q: this.$route.params,
        page: this.page
      })
      if (res.data?.data?.results?.length === 0) {
        this.finished = true // () - listonload
        return
      }
      this.resultList = [...this.resultList, ...res.data.data.results]
      this.loading = false
    },
    onLoad () { //   
      this.getSearchResult()
      this.page++
    }
  }
}
</script>

<style lang="less" scoped>
.search-result-container {
  padding-top: 46px;
}
</style>
