<template>
  <div class="user-edit-container">
    <!-- Header  -->
    <van-nav-bar
      title=""
      left-arrow
      @click-left="$router.back()"
      fixed
    />

    <!--  -->
    <van-cell-group class="action-card">
      <van-cell title="" is-link center>
        <template #default>
          <van-image
            round
            class="avatar"
            :src="userProfile.photo"
            @click="avatatFn"
          />
          <!-- file  -->
          <input type="file" ref="iptFile" v-show="false" @change="changeFn" />
        </template>
      </van-cell>

      <van-cell
        title=""
        is-link
        :value="userProfile.name"
        @click="changeNameFn"
      />

      <van-cell
        title=""
        is-link
        :value="userProfile.birthday"
        @click="timeShow = true"
      />
    </van-cell-group>
    <!--  -->
    <van-dialog
      v-model="show"
      title=""
      show-cancel-button
      :before-close="closeFn"
    >
      <van-field
        v-focus
        v-model="value"
        placeholder=""
        input-align="center"
      />
    </van-dialog>
    <!--  -->
    <van-popup
      v-model="timeShow"
      round
      position="bottom"
    >
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        title=""
        :min-date="minDate"
        :max-date="maxDate"
        @confirm='updataTime'
        @cancel='timeShow=false'
      />
    </van-popup>
  </div>
</template>

<script>
import {
  getUserProfileAPI,
  changeUserAvatatrAPI,
  changeUserProfileAPI
} from '@/api'
import Notify from '@/ui/Notify'
import moment from 'moment'
export default {
  data () {
    return {
      userProfile: {}, // 
      show: false, // 
      value: '', // 
      timeShow: false, // 
      minDate: new Date(1900, 0, 0), // 
      maxDate: new Date(), // ()
      currentDate: new Date() // (, )
    }
  },
  async created () {
    // 
    const [, res] = await getUserProfileAPI()
    this.userProfile = res.data.data
  },
  methods: {
    avatatFn () {
      // 
      this.$refs.iptFile.click()
    },
    async changeFn (e) {
      // input
      const fd = new FormData()
      fd.append('photo', e.target.files[0])
      const [, res] = await changeUserAvatatrAPI(fd)
      this.userProfile.photo = res.data.data.photo
    },
    changeNameFn () {
      // 
      this.show = true
      this.value = this.userProfile.name
    },
    async closeFn (action, done) {
      if (action === 'cancel') done()
      else {
        if (/^[a-zA-Z\u4e00-\u9fa5]{1,7}$/.test(this.value)) {
          // 
          if (this.userProfile.name !== this.value) {
            // 
            await changeUserProfileAPI({
              name: this.value
            })
            Notify({ type: 'success', message: '' })
            this.userProfile.name = this.value
          } else {
            // 
            Notify({ type: 'danger', message: '' })
            done(false)
          }
          done()
        } else {
          // 
          Notify({ type: 'warning', message: '1~7' })
          done(false)
        }
      }
    },
    async updataTime () {
      this.currentDate = moment(this.currentDate).format('YYYY-MM-DD')
      const [, res] = await changeUserProfileAPI({
        birthday: this.currentDate
      })
      this.userProfile.birthday = res.data.data.birthday
      this.timeShow = false
    }
  }
}
</script>

<style lang="less" scoped>
.user-edit-container {
  padding-top: 46px;
  .avatar {
    width: 50px;
    height: 50px;
  }
}
</style>
