// 
import axios from '@/utils/request'
import store from '@/store'

// 
export const login = data => axios({
  url: '/v1_0/authorizations',
  method: 'POST',
  data
  /**
   * data: data(dataaxios, value, )
   * data: {
   *  mobile: 13900000000,
   *  code: 246810
   * }
   * // axiosdata, axiosdatakey+value ()
   */
})

//  - token
export const refreshToken = () => axios({
  url: '/v1_0/authorizations',
  method: 'PUT',
  headers: {
    Authorization: `Bearer ${store.state.refresh_token}`
  }
})
