<template>
  <div>
    <van-nav-bar title=" - " fixed />
    <div class="top">
      <van-form @submit="onSubmit">
        <van-field
          required
          v-model="user.mobile"
          name="mobile"
          label=""
          placeholder=""
          :rules="[
            {
              required: true,
              message: '',
              pattern: /^1[3-9]\d{9}$/,
            },
          ]"
        />
        <van-field
          required
          v-model="user.code"
          name="code"
          type="number"
          label=""
          placeholder=""
          :rules="[
            {
              required: true,
              message: '6',
              pattern: /^\d{6}$/,
            },
          ]"
        />
        <div style="margin: 16px">
          <van-button
            round
            block
            type="info"
            native-type="submit"
            :loading="isLoding"
            :disabled="isLoding"
            loading-text="..."
            ></van-button
          >
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
import { loginAPI } from '@/api'
import Notify from '@/ui/Notify'
import { mapMutations } from 'vuex'
export default {
  name: 'Login',
  data () {
    return {
      isLoding: false, // 
      user: {
        // : , 
        mobile: '13900000000',
        code: '246810'
      }
    }
  },
  methods: {
    ...mapMutations(['setRefreshToken', 'setToken']),
    async onSubmit (values) {
      this.isLoding = true
      const [err, res] = await loginAPI(values)
      if (err) Notify({ type: 'danger', message: '' })
      else {
        Notify({
          type: 'success',
          message: '',
          onClose: () => {
            this.$router.push({
              name: 'Layout'
            })
          }
        })
        // console.log(res)
        this.setRefreshToken(res.data.data.refresh_token)
        this.setToken(res.data.data.token)
      }
      this.isLoding = false
    }
  }
}
// 3: token, SessionStorage, Vuex
// : vuex
</script>

<style lang="less" scoped>
.top {
  padding-top: 46px;
}
</style>
