import axios from '@/utils/request'

// 
export const artilceDetail = id =>
  axios({
    url: `/v1_0/articles/${id}`
  })

// 
export const autFollowed = data =>
  axios({
    url: '/v1_0/user/followings',
    method: 'POST',
    data
  })

// 
export const unFollowed = data =>
  axios({
    url: `/v1_0/user/followings/${data.target}`,
    method: 'DELETE'
  })

// 
export const artLike = data =>
  axios({
    url: '/v1_0/article/likings',
    method: 'POST',
    data
  })

// 
export const unLike = id =>
  axios({
    url: `/v1_0/article/likings/${id}`,
    method: 'DELETE'
  })

// 
export const getComment = params =>
  axios({
    url: '/v1_0/comments',
    params
  })

// 
export const likeComment = data =>
  axios({
    url: '/v1_0/comment/likings',
    method: 'POST',
    data
  })

// 
export const unLikeComment = id =>
  axios({
    url: `/v1_0/comment/likings/${id}`,
    method: 'DELETE'
  })

// 
export const sendComment = data =>
  axios({
    url: '/v1_0/comments',
    method: 'POST',
    data
  })
