<template>
  <div>
    <div class="search-header">
      <!--  -->
      <van-icon
        name="arrow-left"
        color="white"
        size="18"
        class="goback"
        @click="$router.go(-1)"
      />
      <!--  -->
      <van-search
        v-model.trim="kw"
        placeholder=""
        background="#007BFF"
        shape="round"
        v-focus
        ref="search"
      />
    </div>
    <!--  -->
    <div class="sugg-list" v-if="suggestList.length!==0">
      <div
        class="sugg-item"
        v-for="(item, i) in suggestList"
        :key="i"
        v-html="lightItem(item)"
        @click="$router.push({path:`/search_result/${item}`})"
      ></div>
    </div>
    <!--  -->
    <div class="search-history" v-else>
      <!--  -->
      <van-cell title="">
        <!--  right-icon  -->
        <template #right-icon>
          <van-icon name="delete" class="search-icon" @click="clearHistoryFn"/>
        </template>
      </van-cell>

      <!--  -->
      <div class="history-list" >
        <span class="history-item" v-for="(tag, i) in history" :key="i" @click="localFn(tag)" >{{
          tag
        }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { searchSuggestAPI } from '@/api'
import { setStorage, getStorage } from '@/storage/local'
export default {
  data () {
    return {
      kw: '', // 
      timer: '', // 
      suggestList: [], // 
      history: JSON.parse(getStorage('history')) || [] // 
    }
  },
  watch: {
    kw () { //   
      clearTimeout(this.timer) // 
      if (this.kw.length === 0) {
        //   
        this.suggestList = []
        return
      }
      this.timer = setTimeout(async () => {
        //     
        //   
        this.history = Array.from(new Set([...this.history, this.kw]))
        //   
        // if (this.history.indexOf(this.kw) === -1) {
        //   this.history.push(this.kw)
        // }
        //    
        const [, res] = await searchSuggestAPI({
          q: this.kw
        })
        this.suggestList = res.data.data.options
      }, 500)
    },
    history: { // 
      deep: true,
      handler () {
        setStorage('history', JSON.stringify(this.history))
      }
    }
  },
  methods: {
    lightItem (item) { // 
      const reg = new RegExp(this.kw, 'ig') //    i g
      return item.replace(reg, val => {
        // val
        return `<span style="color:red">${val}</span>`
      })
    },
    localFn (tag) { //     
      this.kw = tag
      this.$refs.search.querySelector('input').focus()
    },
    clearHistoryFn () { // 
      this.history = []
    }
  }
}
</script>

<style scoped lang="less">
.search-header {
  height: 46px;
  display: flex;
  align-items: center;
  background-color: #007bff;
  overflow: hidden;
  /**/
  .goback {
    padding-left: 14px;
  }
  /**/
  .van-search {
    flex: 1;
  }
}
/* */
.sugg-list {
  .sugg-item {
    padding: 0 15px;
    border-bottom: 1px solid #f8f8f8;
    font-size: 14px;
    line-height: 50px;
    // 
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
/** */
.search-icon {
  font-size: 16px;
  line-height: inherit;
}

.history-list {
  padding: 0 10px;
  .history-item {
    display: inline-block;
    font-size: 12px;
    padding: 8px 14px;
    background-color: #efefef;
    margin: 10px 8px 0px 8px;
    border-radius: 10px;
  }
}
</style>
